.peculiarity {
  margin-bottom: 100px;
  &__inner {
    display: flex;
    flex-direction: column;
  }

  &__items {
    margin-bottom: 100px;
    width: 70%;
    margin: 0 auto 100px;
  }

  &__items-title {
    text-align: center;
    font-size: 23px;
    margin-bottom: var(--spacing-large);
  }

  &__item {
    display: flex;
    align-items: center;
    margin-bottom: var(--spacing-large);
    background-color: var(--light-color);
    box-shadow: var(--box-shadow);
    padding: var(--spacing-small) var(--spacing-large);
    border-left: 3px solid var(--orange);
    border-radius: 15px;
  }

  &__title {
    font-weight: normal;
    font-size: 21px;
  }

  &__descr-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__text {
    max-width: 50%;
    line-height: 1.6;
    p {
      margin: 0;
      &:not(:last-child) {
        margin-bottom: var(--spacing-extra-large);
      }
    }
  }
}

.peculiarity-icon {
  width: 40px;
  height: 40px;
  fill: var(--orange);
  margin-right: var(--spacing-large);
}

@include tablet {
  .peculiarity {
    &__inner {
      flex-wrap: wrap;
    }

    &__items {
      width: 100%;
      margin-bottom: var(--spacing-extra-large);
    }
  }
}

@include small-tablet {
  .peculiarity {
    &__inner {
      flex-direction: column-reverse;
    }
    &__items {
      margin-bottom: 0;
    }
    &__descr-inner {
      justify-content: center;
      flex-wrap: wrap;
      margin-bottom: 70px;
    }

    &__text {
      max-width: 100%;
      margin-bottom: var(--spacing-large);
    }
  }
}

@include mobile {
  .peculiarity {
    padding-bottom: 0;
  }
}
